html, body {
    font-family: Arial, Helvetica, sans-serif;
    font-size: 20px;
    margin: 0;
    padding: 0;

    &.clock-visible,
    body {
        overflow: hidden;
    }
}

.player-container {
    width: 80%;
    margin: 2% auto;
}

.current-station {
    font-size: 6rem;
    font-weight: bold;
    margin-bottom: 30px;
    text-align: center;
    text-transform: capitalize;
}

.play-pause-btn {
    background: none;
    border: none;
    border-radius: 5px;
    color: black;
    cursor: pointer;
    display: block;
    font-size: 4rem;
    width: 100%;

    &.play-btn {
        background: lightgreen;
    }

    &.stop-btn {
        background: lightcoral;
    }
}

.station-heading {
    border-top: 3px solid black;
    font-size: 3rem;
    text-align: left;
}

.station-list {
    display: flex;
    flex-wrap: wrap;
    font-size: 4.5rem;
}

.station-item {
    cursor: pointer;
    margin: 30px;
    flex-basis: 30%;
    text-transform: capitalize;

    &:hover {
        color: darkblue;
        text-decoration: underline;
    }

    &.cached {
        color: red;
    }
}

.mode-switch {
    border-top: 2px solid black;
    padding-top: 20px;

    button {
        display: block;
        font-size: 2rem;
        width: 100%;
    }
}

.clock {
    position: fixed;
    top: 5px;
    left: 0;
    
    &.clock-visible {
        width: 100vw;
        height: 100vh;
    }

    img {
        max-width: 40px;
        position: relative;
        left: 10px;
        z-index: 10;
    }

    &-content {
        background-color: rgba(0,0,0, 0.95);
        color: white;
        width: 100%;
        height: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        font-size: 4.5rem;
    }
}